.main-nav__toggle {
  display: none; }

.main-nav__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 880px;
  margin: 0 auto;
  padding: 0 17px;
  list-style: none; }
  .main-nav__list::after {
    position: absolute;
    content: "";
    top: 62px;
    left: 3px;
    width: 871.5px;
    border: 1px solid rgba(255, 255, 255, 0.2); }

.main-nav__item {
  margin-right: 125px;
  padding: 16px 0 4px; }
  .main-nav__item:last-child {
    margin-right: 0; }

.main-nav__link {
  display: block;
  padding: 5px;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: #ffffff;
  opacity: 0.7; }

@media (max-width: 1023px) {
  .main-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    padding: 25px;
    background-color: #0ad9c6;
    border: none;
    z-index: 3;
    cursor: pointer; }
    .main-nav__toggle::before {
      position: absolute;
      content: "";
      top: 15px;
      left: 14px;
      width: 22.5px;
      height: 2px;
      background-color: #000000;
      box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000; }
  .main-nav__toggle--active {
    background-color: transparent; }
    .main-nav__toggle--active::before {
      display: none; }
    .main-nav__toggle--active::after {
      position: absolute;
      content: "";
      top: 10px;
      right: 10px;
      width: 30px;
      height: 30px;
      background-image: url("../img/icon-close.svg");
      background-repeat: no-repeat;
      background-position: center; }
  .main-nav__list {
    position: absolute;
    display: none;
    flex-direction: column;
    left: 0;
    width: 100%;
    padding: 100px 0 135px;
    background-color: #ffffff; }
    .main-nav__list::after {
      display: none; }
  .main-nav__list--active {
    display: block;
    z-index: 2; }
  .main-nav__item {
    margin-right: 0;
    margin-bottom: 30px;
    padding: 0;
    background-color: #f6f6f6; }
    .main-nav__item:last-child {
      margin-bottom: 0; }
  .main-nav__link {
